.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  margin-top: 2rem;
}

#pokemon-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.pokemon-card {
  position: relative;
  width: 12rem;
  height: 12rem;
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.3);
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.pokemon-card:hover {
  transform: scale(1.05);
}

.pokemon-card img {
  width: 10rem;
  height: 10rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pokemon-card p {
  margin-top: 0.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: capitalize;
}

.pokemon-details {
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  font-size: 0.8rem;
  overflow-y: scroll;
  padding: 0.5rem;
  box-sizing: border-box;
}

button {
  margin-top: 2rem;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  background-color: #1f2937;
  color: #fff;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

button:hover {
  transform: scale(1.05);
}
